/* --------------------------------------------------------------------------

    Footer

  -------------------------------------------------------------------------- */
.l-footer {
  padding: 30px 15px;
  @include tab {
    margin-top: 110px;
    padding: 0 0 80px;
  }

  &-link-nus {
    margin: 70px 10px 0;
    position: relative;
    @include tab {
      margin: 0 auto;
      width: 100%;
      max-width: 363px;
    }

    &.is-aniActive {
      animation: fadeIn 1s ease-in forwards;
    }

    &::before {
      position: absolute;
      content: "";
      top: -30px;
      left: 0;
      right: 0;
      z-index: 1;
      margin: 0 auto;
      width: 42px;
      height: 16px;
      background: url(../images/icon_line.svg) no-repeat center center / contain;
      animation: flash 1.2s linear infinite;
    }

    &__inner {
      display: flex;
      justify-content: center;
      align-items: center;
      height: 60px;
      font-size: $fontSize14;
      font-weight: bold;
      line-height: 1;
      color: $whiteColor;
      border-radius: 10px;
      background-color: $blackColor;
      @include tab {
        font-size: $fontSize16;
        transition: .3s ease;
        &:hover {
          color: $mainColor1;
        }
      }
    }
  }

  &-sec {
    @include tab {
      margin: 0 auto;
      max-width: $contentsWidth;
    }
  }

  &-hdg {
    img {
      margin: 0 auto;
      width: calc(117 / 390 * 100vw);
      @include tab {
        width: 117px;
      }
    }
  }

  &-links {
    margin-top: 0;
    display: flex;
    flex-wrap: wrap;
    @include tab {
      flex-wrap: nowrap;
    }

    &__item {
      width: calc((100% - 17px) / 2);
      @include tab {
        margin-right: 6px;
        width: 100%;
      }
    }
    @include sp {
      &__item:nth-child(odd) {
        margin-right: 17px;
      }
      &__item:nth-child(n + 3) {
        margin-top: 17px;
      }
    }
    @include tab {
      &__item:last-child {
        margin-right: 0;
      }
    }

    &__anc {
      display: flex;
      justify-content: center;
      align-items: center;
      height: 60px;
      font-size: clampFs(12, 14, 16, 414);
      font-weight: bold;
      line-height: 1;
      border: 1px solid $grayColor2;
      border-radius: 10px;
      background-color: $whiteColor;
      @include tab {
        height: 40px;
        font-size: $fontSize10;
        transition: .3s ease;
        &:hover {
          background-color: $grayColor2;
        }
      }
    }
  }

  &-copy {
    margin-top: 35px;
    font-size: $fontSize12;
    text-align: center;
    @include tab {
      margin-top: 66px;
    }
  }
}
