/* --------------------------------------------------------------------------

    Link

   -------------------------------------------------------------------------- */
/* About
   ========================================================================== */
.c-link-about {
  margin: 22px 0 0 -5px;
  @include tab {
    margin: 96px -10px 0 0;
    text-align: right;
  }

  &.is-aniActive {
    animation: fadeRight 1.5s forwards;
  }

  &__inner {
    display: inline-block;
    width: calc(100 / 390 * 100vw);
    position: relative;
    @include tab {
      padding-right: 74px;
      width: auto;
      transition: .3s ease;
      &:hover {
        transform: translateX(10px);
      }

      img {
        width: 163px;
      }
    }

    &::before, &::after {
      position: absolute;
      content: "";
      right: -44px;
      z-index: 1;
      height: 1px;
      background-color: $blackColor;
    }
    &::before {
      bottom: -6px;
      width: calc(109 / 390 * 100vw);
      @include tab {
        right: 0;
        width: 181px;
      }
    }
    &::after {
      bottom: -2px;
      width: 10px;
      transform: rotate(45deg);
      @include tab {
        right: -1px;
        bottom: 0;
        width: 16px;
      }
    }
  }
}

/* Movie
   ========================================================================== */
.c-link-uf {
  margin-top: 27px;
  text-align: right;
  @include tab {
    margin-top: 40px;
    text-align: center;
  }

  &.is-aniActive {
    animation: fadeRight 1.5s forwards;
  }

  &__inner {
    margin-right: 27px;
    display: inline-block;
    position: relative;
    @include tab {
      margin-right: 0;
      transition: .3s ease;
      &:hover {
        transform: translateX(10px);
      }
    }

    &::before, &::after {
      position: absolute;
      content: "";
      right: 0;
      z-index: 1;
      height: 1px;
      background-color: $blackColor;
      @include tab {
        right: -38px;
      }
    }
    &::before {
      bottom: -6px;
      width: calc(48 / 390 * 100vw);
      @include tab {
        width: 71px;
      }
    }
    &::after {
      bottom: -2px;
      width: 8px;
      transform: rotate(45deg);
      @include tab {
        width: 11px;
      }
    }
    img {
      padding-right: 18px;
      width: calc(60 / 390 * 100vw);
      @include tab {
        padding-right: 0;
        width: 60px;
      }
    }
  }
}