/* --------------------------------------------------------------------------

    Heading

   -------------------------------------------------------------------------- */
/* H1
   ========================================================================== */
.c-hdg-lv1 {
  position: absolute;
  top: 10px;
  left: 0;
  right: 0;
  z-index: 9999;
  margin: 0 auto;
  width: calc(89 / 390 * 100vw);
  @include tab {
    top: 20px;
    width: 103px;
  }

  &__anc {
    display: block;

    &::before {
      position: absolute;
      content: "";
      top: 0;
      left: 0;
      z-index: 9999;
      width: 100%;
      height: 100%;
      background: url(../images/txt_nus_black.svg) no-repeat center center / contain;
      opacity: 0;
      transition: .4s;
    }
  }
  .is-MenuOpen &__anc::before {
    opacity: 1;
  }
}

/* H2
   ========================================================================== */
// first view
.c-hdg-fv {
  position: absolute;
  top: calc(115 / 390 * 100vw);
  left: 0;
  right: 0;
  z-index: 10;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: $whiteColor;
  @include tab {
    top: 110px;
    margin: 0 auto;
    padding: 0 60px;
    width: 90%;
    max-width: $contentsWidth;
    align-items: flex-start;
  }

  &__jp, &__en {
    display: block;
    width: calc(170 / 390 * 100vw);
    text-shadow: 0 0 5px $alphaColor1;
    @include tab {
      width: 188px;
    }
  }
  &__jp {
    font-size: $fontSize14;
    @include lh(14, 26);
    @include tab {
      font-size: $fontSize15;
    }
  }
  &__en {
    margin-top: 11px;
    font-size: $fontSize10;
    font-weight: normal;
    @include lh(10, 16);
    @include tab {
      margin-top: 8px;
      font-size: $fontSize11;
    }
  }
  .ani-typewriter {
    display: block;
    width: 0;
    white-space: nowrap;
    overflow: hidden;
    opacity: 0;
  }
  .is-active & {
    .ani-typewriter._first {
      animation: typing 1.5s steps(12, end) forwards;
    }
    .ani-typewriter._second {
      animation: typing 1.5s 1.5s steps(9, end) forwards;
    }
    .ani-typewriter._third {
      animation: typing 1.5s 3s steps(25, end) forwards;
    }
    .ani-typewriter._fourth {
      animation: typing 1.5s 4.5s steps(13, end) forwards;
    }
  }
}

// About
.c-hdg-about {
  margin-left: 22px;
  padding-right: 20px;
  display: inline-block;
  width: calc(107 / 390 * 100vw);
  position: relative;
  @include tab {
    margin-left: 126px;
    width: 238px;
  }

  &.is-aniActive {
    animation: fadeRight 1.5s forwards;
  }

  &::after {
    position: absolute;
    content: "Nogamifarm Uphill Store";
    left: 16px;
    bottom: -18px;
    z-index: 1;
    font-size: $fontSize9;
    font-weight: normal;
    white-space: pre;
    @include tab {
      font-size: $fontSize17;
      left: 85px;
      bottom: -30px;
    }
  }
}

// Movie
.c-hdg-uf {
  background-color: $whiteColor;
  position: relative;
  @include tab {
    display: inline-block;
    width: 390px;
    height: 66px;
    border-radius: 33px;
  }

  &::after {
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    right: 0;
    z-index: 0;
    margin: 0 auto;
    width: 0;
    height: 100%;
    background-color: $blackColor;
    @include tab {
      border-radius: 33px;
    }
  }
  &.is-aniActive {
    opacity: 1;
    &::after {
      animation: w100 .7s linear forwards;
    }
  }

  &__logo {
    margin: 0 auto;
    width: calc(132 / 390 * 100vw);
    position: relative;
    z-index: 2;
    @include tab {
      width: 132px;
    }
  }
  &__movie {
    position: absolute;
    right: calc(26 / 390 * 100vw);
    bottom: 13px;
    z-index: 2;
    width: calc(33 / 390 * 100vw);
    height: calc(11 / 390 * 100vw);
    @include tab {
      right: 26px;
      width: 33px;
      height: 10px;
    }
  }
}

// Product
.c-hdg-ph {
  display: flex;
  align-items: center;
  position: absolute;
  top: 50%;
  left: 23px;
  z-index: 1;
  transform: translateY(-50%);

  &::before {
    position: relative;
    content: "";
    top: 0;
    left: 0;
    z-index: 1;
    width: calc(97 / 390 * 100vw);
    height: calc(96 / 390 * 100vw);
    background: url(../images/logo_nus.svg) no-repeat center center / calc(97 / 390 * 100vw) calc(96 / 390 * 100vw);
    @include tab {
      width: 105px;
      height: 104px;
      background-size: 105px 104px;
    }
  }
  &.is-aniActive {
    animation: fadeIn .7s ease-in forwards;
  }

  img {
    margin-left: 11px;
    width: calc(175 / 390 * 100vw);
    opacity: 0;
    @include tab {
      width: 185px;
    }
  }

  &.is-aniActive img {
    animation: fadeIn .7s .6s ease-in forwards;
  }
}

// Recruit
.c-hdg-recruit {
  position: absolute;
  top: calc(60 / 390 * 100vw);
  left: 0;
  right: 0;
  z-index: 1;
  margin: 0 auto;
  width: calc(166 / 390 * 100vw);
  @include tab {
    top: 65px;
    width: 179px;
  }

  &.is-aniActive {
    animation: fadeRight 1.5s forwards;
  }
}

