/* --------------------------------------------------------------------------

    Utility

   -------------------------------------------------------------------------- */
/* Align
   ========================================================================== */
.u-align--center {
  text-align: center;
}
@include tab {
  .u-align--center-pc {
    text-align: center;
  }
}
.u-align--left {
  text-align: left;
}
.u-align--right {
  text-align: right;
}
.u-flexVerticalAlign--center {
  align-items: center;
}

/* Display
   ========================================================================== */
.u-spHide {
  display: none;

  @include tab {
    display: block;
  }
}
.u-pcHide {
  @include tab {
    display: none;
  }
}
