/* splide
   ========================================================================== */
@keyframes splide-loading{0%{transform:rotate(0)}to{transform:rotate(1turn)}}.splide__track--draggable{-webkit-touch-callout:none;-webkit-user-select:none;-ms-user-select:none;user-select:none}.splide__track--fade>.splide__list{display:block}.splide__track--fade>.splide__list>.splide__slide{left:0;opacity:0;position:absolute;top:0;z-index:0}.splide__track--fade>.splide__list>.splide__slide.is-active{opacity:1;position:relative;z-index:1}.splide--rtl{direction:rtl}.splide__track--ttb>.splide__list{display:block}.splide__container{box-sizing:border-box;position:relative}.splide__list{-webkit-backface-visibility:hidden;backface-visibility:hidden;display:-ms-flexbox;display:flex;height:100%;margin:0!important;padding:0!important}.splide.is-initialized:not(.is-active) .splide__list{display:block}.splide__pagination{-ms-flex-align:center;align-items:center;display:-ms-flexbox;display:flex;-ms-flex-wrap:wrap;flex-wrap:wrap;-ms-flex-pack:center;justify-content:center;margin:0;pointer-events:none}.splide__pagination li{display:inline-block;line-height:1;list-style-type:none;margin:0;pointer-events:auto}.splide__progress__bar{width:0}.splide{position:relative;visibility:hidden}.splide.is-initialized,.splide.is-rendered{visibility:visible}.splide__slide{-webkit-backface-visibility:hidden;backface-visibility:hidden;box-sizing:border-box;-ms-flex-negative:0;flex-shrink:0;list-style-type:none!important;margin:0;position:relative}.splide__slide img{vertical-align:bottom}.splide__spinner{animation:splide-loading 1s linear infinite;border:2px solid #999;border-left-color:transparent;border-radius:50%;bottom:0;contain:strict;display:inline-block;height:20px;left:0;margin:auto;position:absolute;right:0;top:0;width:20px}.splide__sr{clip:rect(0 0 0 0);border:0;height:1px;margin:-1px;overflow:hidden;padding:0;position:absolute;width:1px}.splide__toggle.is-active .splide__toggle__play,.splide__toggle__pause{display:none}.splide__toggle.is-active .splide__toggle__pause{display:inline}.splide__track{overflow:hidden;position:relative;z-index:0}

.splide__slide {
  height: 100vh;
  overflow: hidden;

  .type-ios & {
    height: 100svh;
  }

  &.is-active {
    animation: zoomOut 12s linear 0s 1 normal both;
  }

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.c-splide {
  height: 100vh;
  position: relative;

  .type-ios & {
    height: 100svh;
  }

  &::before, &::after {
    position: absolute;
    content: "";
    z-index: 9999;
  }
  &::before {
    top: 10px;
    left: 10px;
    width: calc(68 / 390 * 100vw);
    height: calc(100% - 20px);
    border-left: 1px solid $whiteColor;
    border-bottom: 1px solid $whiteColor;
    @include tab {
      top: 20px;
      left: 30px;
      width: 63px;
      height: calc(100% - 40px);
    }
  }
  &::after {
    top: 33px;
    right: 10px;
    width: 1px;
    height: calc(100% - 106px);
    border-right: 1px solid $whiteColor;
    @include tab {
      top: 58px;
      right: 30px;
      height: calc(100% - 145px);
    }
  }

  .splide__track {
    &::before, &::after {
      position: absolute;
      content: "";
      top: 10px;
      z-index: 9999;
      height: 1px;
      background-color: $whiteColor;
      @include tab {
        top: 20px;
      }
    }
    &::before {
      left: 10px;
      width: calc(118 / 390 * 100vw);
      @include tab {
        left: 30px;
        width: calc(285 / 768 * 100vw);
      }
      @include pc {
        width: calc(515 / 1200 * 100vw);
      }
    }
    &::after {
      right: 48px;
      width: calc(81 / 390 * 100vw);
      @include tab {
        right: 90px;
        width: calc(228 / 768 * 100vw);
      }
      @include pc {
        width: calc(458 / 1200 * 100vw);
      }
    }
  }

  .is-MenuOpen & {
    &::before, &::after {
      border-color: $blackColor;
    }
    .splide__track {
      position: static;
    }
    .splide__track::before, .splide__track::after {
      background-color: $blackColor;
    }
  }
}

/* Lightbox
   ========================================================================== */
.c-lightbox {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9999;
  width: 100%;
  height: 100%;
  background: $alphaColor4;
  pointer-events: none;
  opacity: 0;
  transition: .25s ease-out;

  &.is-show {
    opacity: 1;
    pointer-events: all;

    .c-lightbox__img {
      opacity: 1;
    }
  }

  &__btn {
    position: absolute;
    top: 0;
    left: 0;
    width: 50px;
    height: 50px;
    color: $transparentColor;
    background: url(../images/icon_close.svg) no-repeat 15px 12px / auto;
    @include tab {
      left: 10px;
    }
  }

  &__img {
    position: absolute;
    top: 50%;
    left: 50%;
    max-width: 540px;
    max-height: 540px;
    transform: translate(-50%, -50%);
    transition: .5s ease-out;
    opacity: 0;
  }
}