/* --------------------------------------------------------------------------

    List

   -------------------------------------------------------------------------- */
/* About
   ========================================================================== */
.c-list-about {
  margin-top: 50px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  @include tab {
    margin-top: 90px;
    flex-wrap: nowrap;
  }

  &__item {
    width: calc((100% - 40px) / 2);
    @include tab {
      margin-right: 57px;
      width: calc((100% - 57px * 3) / 4);
    }
  }
  @include sp {
    &__item:nth-child(odd) {
      margin-right: 40px;
    }
    &__item:nth-child(n + 3) {
      margin-top: 70px;
    }
  }
  @include tab {
    &__item:last-child {
      margin-right: 0;
    }
  }
  &__item.is-aniActive {
    animation: fadeUp 1s forwards;
  }
  &__item.is-aniActive:nth-child(2) {
    animation-delay: .5s;
  }
  &__item.is-aniActive:nth-child(3) {
    animation-delay: .5s;
  }
  &__item.is-aniActive:nth-child(4) {
    animation-delay: 1s;
  }

  &__ttl {
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 132px;
    height: 37px;
    font-size: $fontSize13;
    font-weight: bold;
    border: 1px solid $grayColor3;
    background-color: $whiteColor;
    @include tab {
      width: 100%;
      max-width: 156px;
      height: 44px;
      font-size: $fontSize15;
    }
  }
  &__contents {
    margin-top: 20px;
    font-size: $fontSize14;
    @include lh(14, 25);
    text-align: center ;
    @include tab {
      margin-top: 40px;
      font-size: $fontSize16;
    }
  }
}

/* Movie
   ========================================================================== */
.c-list-movie {
  margin: 27px 0 0 7px;
  display: flex;
  overflow-x: scroll;
  @include tab {
    margin: 65px 0 0;
    flex-wrap: wrap;
    overflow-x: visible;
  }

  &__item {
    margin-right: 20px;
    width: calc(320 / 390 * 100vw);
    flex: 1 0 auto;
    position: relative;
    overflow: hidden;
    @include tab {
      width: calc((100% - 20px * 2) / 3);
      flex: none;
    }

    &::before, &::after {
      position: absolute;
      content: "";
      right: calc(34 / 390 * 100vw);
      bottom: 14px;
      @include tab {
        right: 34px;
      }
    }

    &::before {
      z-index: 11;
      margin: 0 5px 6px 0;
      width: 0;
      height: 0;
      border-style: solid;
      border-width: calc(6 / 390 * 100vw) 0 calc(6 / 390 * 100vw) calc(12 / 390 * 100vw);
      border-color: $transparentColor $transparentColor $transparentColor $blackColor;
      @include tab {
        border-width: 6px 0 6px 12px;
      }
    }

    &::after {
      z-index: 10;
      width: calc(25 / 390 * 100vw);
      height: calc(25 / 390 * 100vw);
      border-radius: 50%;
      background-color: $mainColor1;
      @include tab {
        width: 25px;
        height: 25px;
      }
    }
  }
  @include sp {
    &__item:last-child {
      margin-right: 7px;
    }
  }
  @include tab {
    &__item:nth-child(3n) {
      margin-right: 0;
    }
    &__item:nth-child(n + 4) {
      margin-top: 30px;
    }
  }
  &__item.is-active {
    padding-top: 56.25%;
    position: relative;
    @include tab {
      padding-top: 0;
    }

    iframe {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
  }
  &__anc {
    display: block;
    @include tab {
      cursor: pointer;
      transition: .3s ease;
      &:hover {
        opacity: .8;
      }
    }

    img {
      height: 100%;
      border-radius: 10px;
      object-fit: cover;
      transform: scale(1.2);
    }
  }
  .is-disabled &__anc {
    pointer-events: none;
  }
  &__item.is-disabled::before, &__item.is-disabled::after {
    content: none;
    width: 0;
    height: 0;
  }
}

/* Product
   ========================================================================== */
.c-list-ph {
  margin: 10px 7px 0;

  @include sp {
    display: grid;
    grid-row-gap: 8px;
    grid-column-gap: 8px;

    & + & {
      margin: 8px 7px 0;
    }
    &._sp-col3 {
      grid-template-columns: repeat(3, 1fr);
    }
    &._sp-col4 {
      grid-template-columns: repeat(4, 1fr);
    }
    &__item._item09 {
      grid-row: 3 /5;
      grid-column: 2 /4;
    }
    &__item._item10 {
      grid-row: 1 /3;
      grid-column: 1 /3;
    }
    &__item._item15 {
      grid-row: 3 /5;
      grid-column: 2 /4;
    }
    &__item._item24 {
      grid-row: 1 /4;
      grid-column: 1 /4;
    }
    &__item._item25 {
      grid-row: 4 /6;
      grid-column: 1 /3;
    }

    &._col3 {
      .c-list-ph__item.is-aniActive._item02 {
        animation-delay: .5s;
      }
      .c-list-ph__item.is-aniActive._item03 {
        animation-delay: 1s;
      }
      .c-list-ph__item.is-aniActive._item04 {
        animation-delay: .5s;
      }
      .c-list-ph__item.is-aniActive._item05 {
        animation-delay: 1.2s;
      }
      .c-list-ph__item.is-aniActive._item06 {
        animation-delay: 1.5s;
      }
      .c-list-ph__item.is-aniActive._item07 {
        animation-delay: .5s;
      }
      .c-list-ph__item.is-aniActive._item08 {
        animation-delay: 1.5s;
      }
      .c-list-ph__item.is-aniActive._item09 {
        animation-delay: 1.2s;
      }
    }
    &._sp-col3._pc-col6 {
      .c-list-ph__item.is-aniActive._item10 {
        animation-delay: .5s;
      }
      .c-list-ph__item.is-aniActive._item11 {
        animation-delay: 1s;
      }
      .c-list-ph__item.is-aniActive._item12 {
        animation-delay: 1.3s;
      }
      .c-list-ph__item.is-aniActive._item13 {
        animation-delay: .5s;
      }
      .c-list-ph__item.is-aniActive._item14 {
        animation-delay: 1.5s;
      }
      .c-list-ph__item.is-aniActive._item15 {
        animation-delay: 1.2s;
      }
    }
    &._sp-col4 {
      .c-list-ph__item.is-aniActive._item16 {
        animation-delay: .5s;
      }
      .c-list-ph__item.is-aniActive._item17 {
        animation-delay: .8s;
      }
      .c-list-ph__item.is-aniActive._item18 {
        animation-delay: 1.1s;
      }
      .c-list-ph__item.is-aniActive._item19 {
        animation-delay: 1.4s;
      }
      .c-list-ph__item.is-aniActive._item20 {
        animation-delay: .5s;
      }
      .c-list-ph__item.is-aniActive._item21 {
        animation-delay: 1.2s;
      }
      .c-list-ph__item.is-aniActive._item22 {
        animation-delay: 1.5s;
      }
      .c-list-ph__item.is-aniActive._item23 {
        animation-delay: 1.7s;
      }
    }
    &._sp-col3.u-pcHide {
      .c-list-ph__item.is-aniActive._item24 {
        animation-delay: .5s;
      }
      .c-list-ph__item.is-aniActive._item25 {
        animation-delay: .8s;
      }
      .c-list-ph__item.is-aniActive._item26 {
        animation-delay: 1s;
      }
      .c-list-ph__item.is-aniActive._item27 {
        animation-delay: 1.3s;
      }
    }
  }

  @include tab {
    margin: 0;

    &._col3 {
      display: grid;
      grid-row-gap: 10px;
      grid-column-gap: 10px;
      grid-template-columns: repeat(3, 1fr);
      width: calc((100% - 18px) / 2);

      .c-list-ph__item.is-aniActive._item02 {
        animation-delay: .5s;
      }
      .c-list-ph__item.is-aniActive._item03 {
        animation-delay: 1s;
      }
      .c-list-ph__item.is-aniActive._item04 {
        animation-delay: .5s;
      }
      .c-list-ph__item.is-aniActive._item05 {
        animation-delay: 1.2s;
      }
      .c-list-ph__item.is-aniActive._item06 {
        animation-delay: 1.5s;
      }
      .c-list-ph__item.is-aniActive._item07 {
        animation-delay: .5s;
      }
      .c-list-ph__item.is-aniActive._item08 {
        animation-delay: 1.5s;
      }
      .c-list-ph__item.is-aniActive._item09 {
        animation-delay: 1.2s;
      }
    }
    &._pc-col6 {
      margin: 8px 0 0 -13px;
      width: calc(100% + 13px);
      display: flex;
      flex-wrap: wrap;

      .c-list-ph__item {
        margin: 10px 0 0 13px;
        width: calc((100% - 13px * 6) / 6);
        flex: 1 0 auto;
      }

      & + & {
        margin-top: 12px;
      }
    }
    &._sp-col3._pc-col6 {
      .c-list-ph__item.is-aniActive._item10 {
        animation-delay: .5s;
      }
      .c-list-ph__item.is-aniActive._item11 {
        animation-delay: .8s;
      }
      .c-list-ph__item.is-aniActive._item12 {
        animation-delay: 1.1s;
      }
      .c-list-ph__item.is-aniActive._item24 {
        animation-delay: 1.4s;
      }
      .c-list-ph__item.is-aniActive._item13 {
        animation-delay: 1.7s;
      }
      .c-list-ph__item.is-aniActive._item14 {
        animation-delay: .5s;
      }
      .c-list-ph__item.is-aniActive._item15 {
        animation-delay: 2s;
      }
      .c-list-ph__item.is-aniActive._item16 {
        animation-delay: 1.2s;
      }
      .c-list-ph__item.is-aniActive._item17 {
        animation-delay: 1.5s;
      }
      .c-list-ph__item.is-aniActive._item18 {
        animation-delay: 1.7s;
      }
      .c-list-ph__item.is-aniActive._item19 {
        animation-delay: 1.9s;
      }
      .c-list-ph__item.is-aniActive._item20 {
        animation-delay: 2.1s;
      }
      .c-list-ph__item.is-aniActive._item21 {
        animation-delay: .5s;
      }
      .c-list-ph__item.is-aniActive._item22 {
        animation-delay: 1s;
      }
      .c-list-ph__item.is-aniActive._item23 {
        animation-delay: 1.2s;
      }
      .c-list-ph__item.is-aniActive._item25 {
        animation-delay: 1.5s;
      }
      .c-list-ph__item.is-aniActive._item26 {
        animation-delay: 1.7s;
      }
    }
    &__item {
      cursor: pointer;
      position: relative;

      &::before, &::after {
        position: absolute;
        content: "";
        top: 0;
        left: 0;
        pointer-events: none;
        transition: .3s ease;
      }

      &::before {
        z-index: 1;
        width: 100%;
        height: 100%;
        border-radius: 10px;
        background-color: $transparentColor;
      }
      &:hover::before {
        background-color: $alphaColor3;
      }
      &::after {
        right: 0;
        bottom: 0;
        z-index: 2;
        margin: auto;
        width: 60px;
        height: 60px;
        background: url(../images/icon_plus.svg) no-repeat center center / contain;
        opacity: 0;
      }
      &:hover::after {
        opacity: 1;
      }
    }
    &__item._item08 {
      order: 9;
    }
    &__item._item09 {
      order: 8;
    }
    &__item._item13 {
      order: 5;
    }
    &__item._item14 {
      order: 7;
    }
    &__item._item15 {
      order: 6;
    }
    &__item._item16 {
      order: 8;
    }
    &__item._item17 {
      order: 9;
    }
    &__item._item18 {
      order: 10;
    }
    &__item._item19 {
      order: 11;
    }
    &__item._item20 {
      order: 12;
    }
    &__item._item21 {
      order: 13;
    }
    &__item._item22 {
      order: 14;
    }
    &__item._item23 {
      order: 15;
    }
    &__item._item24 {
      order: 4;
    }
    &__item._item25 {
      order: 16;
    }
    &__item._item26 {
      order: 17;
    }
    &__item._item27 {
      display: none;
    }
  }

  @include pc {
    &._col3 {
      width: calc(100% - 440px);
    }
  }

  &__item {
    &.is-aniActive {
      animation: fadeUp 1s forwards;
    }

    picture {
      display: flex;
      height: 100%;
    }

    img {
      border-radius: 10px;
      object-fit: cover;
    }
  }
}

/* Recruit
   ========================================================================== */
.c-list-recruit {
  padding: 0 10px;
  display: flex;
  flex-wrap: wrap;
  @include tab {
    padding: 0;
  }

  &__item {
    width: calc((100% - 8px) / 2);
    @include tab {
      margin-right: 10px;
      width: calc((100% - 10px * 2) / 3);
    }

    img {
      border-radius: 10px;
    }
  }
  @include sp {
    &__item:nth-child(odd) {
      margin-right: 8px;
    }
    &__item:nth-child(n + 3) {
      margin-top: 10px;
    }

    &__item.is-aniActive:nth-child(3) {
      animation-delay: .5s;
    }
    &__item.is-aniActive:nth-child(4) {
      animation-delay: 1.2s;
    }
    &__item.is-aniActive:nth-child(5) {
      animation-delay: .8s;
    }
    &__item.is-aniActive:nth-child(6) {
      animation-delay: 1.2s;
    }
  }
  @include tab {
    &__item:nth-child(3n) {
      margin-right: 0;
    }
    &__item:nth-child(n + 4) {
      margin-top: 10px;
    }

    &__item.is-aniActive:nth-child(3) {
      animation-delay: 1s;
    }
    &__item.is-aniActive:nth-child(4) {
      animation-delay: .5s;
    }
    &__item.is-aniActive:nth-child(5) {
      animation-delay: 1.2s;
    }
    &__item.is-aniActive:nth-child(6) {
      animation-delay: 1.5s;
    }
  }

  &__item.is-aniActive {
    animation: fadeUp 1s forwards;
  }
  &__item.is-aniActive:nth-child(2) {
    animation-delay: .5s;
  }
}