/* --------------------------------------------------------------------------

    Section / Area

  -------------------------------------------------------------------------- */
/* First view
  ========================================================================== */
@import url('https://fonts.googleapis.com/css2?family=Anton&display=swap');
.c-sec-fv {
  position: relative;

  &::before {
    position: absolute;
    content: "Nogamifarm Uphill Store";
    left: 0;
    bottom: 3px;
    z-index: 9999;
    width: 100%;
    text-align: center;
    font-size: $fontSize20;
    font-family: 'Anton', sans-serif;
    text-shadow: 0 0 5px $alphaColor1;
    letter-spacing: .05em;
    color: $whiteColor;
    opacity: 0;
    @include tab {
      left: 103px;
      bottom: 15px;
      width: auto;
    }
  }

  &.is-active::before {
    animation: fadeRight 1.5s forwards;
  }
  .is-MenuOpen &::before {
    color: $blackColor;
  }
}

/* About
  ========================================================================== */
.c-sec-about {
  padding: 45px 40px 56px;
  background-color: $whiteColor;
  @include tab {
    margin: 0 auto;
    padding: 65px 20px 110px;
    max-width: $contentsWidth;
  }
}

/* Movie
  ========================================================================== */
.c-sec-uf {
  padding-bottom: 38px;
  @include tab {
    padding-bottom: 110px;
    text-align: center;
  }
}

/* Product
  ========================================================================== */
.c-sec-ph {
  padding-bottom: 23px;
  @include tab {
    margin: 0 auto;
    padding-bottom: 110px;
    max-width: $contentsWidth;
    display: flex;
    flex-wrap: wrap;
  }

  .c-ph-hdg-wrap {
    position: relative;
    @include tab {
      margin-right: 18px;
      width: calc((100% - 18px) / 2);
    }
    @include pc {
      width: 422px;
    }

    .c-bg-ph img {
      height: calc(520 / 390 * 100vw);
      object-fit: cover;
      @include tab {
        width: 100%;
        height: 563px;
      }
    }
  }
}

/* Recruit
  ========================================================================== */
.c-sec-recruit {

  @include tab {
    margin: 0 auto;
    max-width: $contentsWidth;
    display: flex;
    justify-content: space-between;
  }

  &__main {
    position: relative;
    @include tab {
      margin-right: 18px;
      width: calc((100% - 18px) / 2);
    }
    @include pc {
      width: 422px;
    }
  }

  &__foto {
    margin-top: -50px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    @include tab {
      margin-top: 0;
      width: calc((100% - 18px) / 2);
    }
    @include pc {
      width: 560px;
    }
  }

  .c-recruit-hdg-wrap {
    position: relative;

    .c-bg-ph img {
      height: calc(520 / 390 * 100vw);
      object-fit: cover;
      @include tab {
        width: 100%;
        height: 563px;
      }
    }
  }
}