/* --------------------------------------------------------------------------

    Linklist

   -------------------------------------------------------------------------- */
/* Recruit
   ========================================================================== */
.c-linklist-recruit {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 65px;
  z-index: 1;
  margin: 0 auto;
  margin-top: 237px;
  display: flex;
  @include tab {
    bottom: 40px;
    margin-top: 270px;
  }

  &.is-aniActive {
    animation: fadeUp .8s ease-in forwards;
  }

  &__item {
    width: 100%;
    text-align: center;
  }
  &__anc {
    display: inline-block;
    transition: .3s ease;
    &:hover {
      opacity: .7;
    }

    .insta {
      width: 115px;
    }
    .tel {
      width: 116px;
    }
  }
}