/* --------------------------------------------------------------------------

    Text

   -------------------------------------------------------------------------- */
/* scroll
   ========================================================================== */
.c-scroll {
  position: absolute;
  right: 10px;
  bottom: 10px;
  z-index: 10;
  padding-right: 5px;
  width: 17px;
  height: 56px;
  font-size: $fontSize10;
  letter-spacing: .05em;
  text-shadow: 0 0 5px $alphaColor1;
  -webkit-writing-mode: vertical-rl;
  writing-mode: vertical-rl;
  color: $whiteColor;
  overflow: hidden;
  @include tab {
    right: 30px;
    bottom: 20px;
  }

  &::before, &::after {
    position: absolute;
    content: "";
    width: 1px;
    background-color: $whiteColor;
  }
  &::before {
    top: 0;
    right: 0;
    height: 100%;
    animation: scroll 1.2s ease-in-out infinite;
    opacity: 0;
  }
  &::after {
    right: 2px;
    bottom: -1px;
    height: 8px;
    transform: rotate(-45deg);
  }
}

/* About
   ========================================================================== */
.c-ttl-about {
  margin-top: 30px;
  display: flex;
  align-items: center;
  font-size: $fontSize13;
  font-weight: bold;
  @include tab {
    margin-top: 96px;
    justify-content: center;
    font-size: $fontSize22;
  }

  &__en {
    margin-left: 28px;
    @include tab {
      margin-left: 45px;
    }
  }
}

/* Product
========================================================================== */
.c-txt-ph {
  position: absolute;
  right: 28px;
  bottom: 35px;
  z-index: 1;
  display: flex;
  align-items: flex-end;
  @include tab {
    right: 32px;
    bottom: 38px;
  }

  &__play {
    width: calc(127 / 390 * 100vw);
    @include tab {
      width: 137px;
    }
    &.is-aniActive {
      animation: fadeRight 1s 1s ease-in forwards;
    }
  }
  &__agri {
    width: calc(105 / 390 * 100vw);
    @include tab {
      width: 112px;
    }

    &.is-aniActive {
      animation: fadeIn 1s 2s ease-in forwards;
    }
  }
}

/* Recruit
   ========================================================================== */
.c-txt-recruit, .c-txt-recruit-sub {
  position: absolute;
  left: 0;
  z-index: 1;
  width: 100%;
  text-align: center;
  color: $whiteColor;
}
.c-txt-recruit {
  top: calc(124 / 390 * 100vw);
  font-size: $fontSize14;
  @include tab {
    top: 137px;
    font-size: $fontSize15;
  }

  &.is-aniActive {
    animation: fadeUp .8s 1s ease-in forwards;
  }
}
.c-txt-recruit-sub {
  top: calc(151 / 390 * 100vw);
  font-size: $fontSize10;
  @include lh(10, 24);
  @include tab {
    top: 175px;
    font-size: $fontSize12;
  }

  &.is-aniActive {
    animation: fadeUp .8s 1.2s ease-in forwards;
  }
}