/* --------------------------------------------------------------------------

    Navigation

   -------------------------------------------------------------------------- */
.l-gnav {

  &-btn {
    position: absolute;
    top: 10px;
    right: 10px;
    z-index: 10;
    width: 35px;
    height: 20px;
    font-size: $fontSize10;
    font-weight: bold;
    line-height: 1;
    color: $whiteColor;
    @include tab {
      top: 20px;
      right: 30px;
      width: 45px;
      height: 25px;
      font-size: $fontSize13;
    }

    &::before, &::after {
      position: absolute;
      content: "";
      display: block;
      width: 27px;
      height: 1px;
      background-color: $whiteColor;
      transition: .4s;
      @include tab {
        width: 35px;
      }
    }
    &::before {
      top: 0;
      left: 0;
    }
    &::after {
      right: 0;
      bottom: 0;
    }
    .l-gnav-btn__inner {
      display: block;
      padding-top: 2px;
    }
  }

  &-menu {
    padding: 107px 45px 30px;
    height: 100vh;
    display: none;
    background-color: $alphaColor2;
    opacity: 0;
    @include tab {
      padding-top: 137px;
    }

    .type-ios & {
      height: 100svh;
    }
  }

  &-list {
    @include tab {
      margin: 0 auto;
      width: 70%;
      min-width: 500px;
    }
    @include pc {
      width: 500px;
    }

    &__item + &__item {
      margin-top: 40px;
      @include tab {
        margin-top: 58px;
      }
    }
    &__anc {
      display: inline-block;
      font-size: clampFs(18, 20, 22, 414);
      font-weight: bold;
      font-family: $fontEn;
      color: $blackColor;
      text-decoration: underline;
      @include tab {
        font-size: $fontSize25;
        text-shadow: 1px 0 2px $transparentColor;
        transition: .3s ease;
        &:hover {
          color: $mainColor1;
          text-shadow: 1px 0 2px $alphaColor4;
        }
      }
    }
    &__txt {
      margin-top: 5px;
      font-size: $fontSize10;
      @include lh(10, 16);
    }
  }

  &-home {
    text-align: right;
    @include tab {
      margin: 0 auto;
      width: 70%;
    }
    @include pc {
      width: 700px;
    }

    &__anc {
      padding-right: 20px;
      display: inline-block;
      width: calc(70 / 390 * 100vw);
      max-width: 88px;
      position: relative;
      @include tab {
        width: 88px;
        transition: .3s ease;
        &:hover {
          transform: translateX(10px);
        }
      }

      &::before, &::after {
        position: absolute;
        content: "";
        right: 0;
        z-index: 1;
        height: 1px;
        background-color: $blackColor;
      }
      &::before {
        bottom: -6px;
        width: calc(53 / 390 * 100vw);
        @include tab {
          width: 67px;
        }
      }
      &::after {
        bottom: -4px;
        width: 5px;
        transform: rotate(45deg);
        @include tab {
          width: 6px;
        }
      }
    }
  }

  .is-MenuOpen & {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 9998;
    width: 100%;
    height: 100%;

    &::before, &::after {
      position: absolute;
      content: "";
      right: 10px;
      bottom: 10px;
      z-index: 9999;
      background-color: $blackColor;
      @include tab {
        right: 30px;
        bottom: 20px;
      }
    }
    &::before {
      width: 1px;
      height: 20%;
    }
    &::after {
      width: calc(68 / 390 * 100vw);
      height: 1px;
      @include tab {
        width: calc(100% - 367px);
      }
    }

    .l-gnav-menu {
      display: block;
      opacity: 1;
      animation: gnavFadeIn .3s ease-in forwards;
    }
    .l-gnav-btn::before, .l-gnav-btn::after {
      background-color: $blackColor;
    }
    .l-gnav-btn::before {
      transform: translateY(10px) translateX(9px) rotate(-45deg);
      @include tab {
        transform: translateY(15px) translateX(10px) rotate(-45deg);
      }
    }
    .l-gnav-btn::after {
      transform: translateY(-9px) translateX(1px)  rotate(45deg);
    }
    .l-gnav-btn__inner {
      opacity: 0;
    }
  }
}